.slider-container {
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.slides {
    display: flex;
    transition: transform 0.5s ease-in-out;
}

.slide {
    flex: 0 0 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
    background-color: #f9f9f9; /* Background color for slides */
    border-radius: 8px; /* Rounded corners for slides */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Box shadow for slides */
}

.title {
    text-align: center;
    width: 100%;
    margin: 0 auto;
    word-wrap: break-word;
    font-weight: bold;
    color: #333; /* Title color */
    font-weight: bold;
}

.prefix {
    color: #666; /* Prefix color */
    font-style: italic; /* Italic style for prefix */
}

.slide p {
    max-width: 600px;
    margin: 0 auto; /* Center horizontally */
    word-wrap: break-word;

}

.slide img {
    max-width: 100%; /* Adjusted image width */
    height: auto; /* Maintain aspect ratio */
    margin-bottom: 20px;
    border-radius: 8px; /* Rounded corners for images */
}

.slider-controls {
    margin-top: 20px; /* Increased margin */
    display: flex;
    justify-content: center;
}

.prev-button,
.next-button {
    background-color: #333; /* Button background color */
    color: #fff; /* Button text color */
    border: none;
    cursor: pointer;
    font-size: 24px;
    padding: 10px 20px; /* Increased padding */
    border-radius: 4px; /* Rounded corners */
    transition: background-color 0.3s ease;
}

.prev-button:hover,
.next-button:hover {
    background-color: #555; /* Hover background color */
}

.pagination-dots-button {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.dot-xyz {
    background-color: #bbb;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    margin: 0 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.dot-xyz.active {
    background-color: #333;
}

.dot-xyz:hover {
    background-color: #999;
}

.title-separator {
    border: none;
    border-top: 2px solid #ccc; /* Adjust the color and style of the separator */
    margin: 20px 0; /* Increased margin */
}
.slide {
    position: relative;
}

.slide img {
    max-width: 100%; /* Ensure images are responsive */
    height: auto;
    display: block; /* Ensure images don't overflow container */
    margin-bottom: 20px;
    border-radius: 8px; /* Rounded corners for images */
}
.slide {
    position: relative;
}

.slide .image-wrapper {
    max-width: 100%;
    height: 400px; /* Fixed height for the image */
    overflow: hidden;
}

.slide .slide-image {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensure the image covers the entire container */
}
.slide img {
    max-width: 600px;
    height: 400px;
    margin-bottom: 20px; 
}